












































import { Component, Prop, Vue } from 'vue-property-decorator';
import Page from '@/components/Page.vue';
import PageHeader from '@/components/PageHeader.vue';
import PageBody from '@/components/PageBody.vue';
import PageFooter from '@/components/PageFooter.vue';

@Component({
    components:{
        Page,
        PageHeader,
        PageBody,
        PageFooter,
    }
})
export default class ContactUs extends Vue 
{
  public contactForm: any = {};/* {
    name: 'Dummy name',
    address: 'Dummy address',
    email: 'dummy@gmail.com',
    phone: '5870001234',
    subject: 'Dummy subject',
    message: 'Dummy message',
  };*/

  public submitButtonClicked()
  {
    const scriptURL = 'https://script.google.com/macros/s/AKfycbz3-xMPWqSvELtEiOjaHI_oSaC-0urKG_KjIVoVJ62NfeNwMdnv/exec';
    fetch(scriptURL, { 
      headers: {
        //'Content-Type': 'application/json'
      },
      method: 'POST', 
      body: JSON.stringify(this.contactForm)
    })
    .then(response => {
      alert('Your message has been submitted to us. We will reply you shortly.');
      this.contactForm = {};
    })
    .catch(error => {
      alert('Failed to submit the form, please try it again. Error: ' + error);
    })
    ;
  }
}
